<template>
  <div class="chessboard-wrapper">
    <div class="chessboard-container">
      <!-- Plateau SVG -->

      <!-- <img :src="boardImage" class="board-image" alt="Chess Board" /> -->
<svg class="board-svg" viewBox="0 0 320 320" xmlns="http://www.w3.org/2000/svg">
