  <rect x="0" y="80" width="40" height="40" fill="var(--svg-board-bg)" />
  <rect x="40" y="80" width="40" height="40" fill="var(--svg-board-bc)" />
  <rect x="80" y="80" width="40" height="40" fill="var(--svg-board-bg)" />
  <rect x="120" y="80" width="40" height="40" fill="var(--svg-board-bc)" />
  <rect x="160" y="80" width="40" height="40" fill="var(--svg-board-bg)" />
  <rect x="200" y="80" width="40" height="40" fill="var(--svg-board-bc)" />
  <rect x="240" y="80" width="40" height="40" fill="var(--svg-board-bg)" />
  <rect x="280" y="80" width="40" height="40" fill="var(--svg-board-bc)" />
  <!-- Ligne 5 -->
  <rect x="0" y="120" width="40" height="40" fill="var(--svg-board-bc)" />
  <rect x="40" y="120" width="40" height="40" fill="var(--svg-board-bg)" />
  <rect x="80" y="120" width="40" height="40" fill="var(--svg-board-bc)" />
  <rect x="120" y="120" width="40" height="40" fill="var(--svg-board-bg)" />
  <rect x="160" y="120" width="40" height="40" fill="var(--svg-board-bc)" />
  <rect x="200" y="120" width="40" height="40" fill="var(--svg-board-bg)" />
  <rect x="240" y="120" width="40" height="40" fill="var(--svg-board-bc)" />
